.Home{
    padding-left: 10vw;
    padding-right: 10vw;
    padding-top: 3vh;
    text-align: left;
}

.aboutHome{
    color: blueviolet;
    font-size: 50px;
   text-decoration: none;
}

.latestBlogsHome{
    color: blueviolet;
    font-size: 38px;
}
h2{
    margin: 8px 0px;
    font-weight: 400;
}
h4{
    margin: 8px 0px;
}

.blogHome{
    display: flex;
    justify-content: space-between;
    padding-top: 1vh;
    align-items: center;
    margin-right: 5vw;
    border-bottom: 1px solid gray;
}
.blogHomeDate{
    color: green;
}
.favouritesHome{
    display: flex;
    column-gap: 3vw;
  row-gap: 3vh;
  padding-left: 5vw;
    flex-wrap: wrap;
    
}
.favourite{
    border: 1px solid gray;
    width: 13cm;
    /* height: 4cm; */
    border-radius: 7px;
    box-shadow: rgba(50, 50, 93, 0.25) 0px 6px 12px -2px, rgba(0, 0, 0, 0.3) 0px 3px 7px -3px;
    
}
.favourite:hover{
    transform: scale(1.13 );
    transition: .12s;
    box-shadow:rgba(0,0,0,0.5) 0px 5px 15px, inset rgba(0,0,0,0.1) 0px -10px 20px;
}
.upperFav{
    padding: 0.7vh .7vw 0.7vh .7vw;
    display: flex;
    justify-content: space-between;
    border-bottom: 0.5px solid gray;
}
.linkFav{
    color: blueviolet;
}
.favName{
    margin: 5px;
    font-size: 23px;
    font-weight: 600;
}
.favYear{
    margin: 10px 0px;
    color: green;
    font-weight: 600;
}
.favDesc{
    margin: 10px 0px;
    padding-left: 1vw;
    font-size: 21px;
}
.favButtons{
    display: flex;
    justify-content:center ;
    column-gap: .3cm;
}
.favYear{
    font-family: Ubuntu;
}
.blogHomeDate{
    font-family: Ubuntu;
}
#favButtonInd{
    width: 2.2cm;
    display: flex;
    align-items: center;
    font-size: 20px;
    column-gap: .15cm;
    height: .8cm;
    border: 1px solid gray;
    border-radius: 7px; 
    padding-left: 5px;
}
.directH{
    width: .4cm;
    height: .4cm;
    margin-top: 5px;
    
    
  }
  .formNewsletter{
    display: flex;
    flex-wrap: wrap;
    column-gap: .2cm;
    row-gap: .2cm;
    padding-top: 10px;
  }
.newsForm{
    border: black 1px solid;
    border-radius: 10px;
    width: 7cm;
    height: .8cm;
    font-size: 20px;

}
.newsButton{
    border: none;
    background-color: #10b331;
    color: whitesmoke;
    height: .9cm;
    font-size: 15px;
    border-radius: 10px;
    cursor: pointer;
    /* display: none; */
}
.overlayNews{
    z-index: 100;
    overflow: hidden;
    height: 0vh;
      width: 380px;
    left: 30%;
    top: 30%;
    position: fixed;
   /* border: 1px solid black; */
   background-color: blanchedalmond;
    border: none;
      border-radius: 10px;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
    transition: 0.7s;
  }
  #favButtonInd{
    box-shadow: rgba(50, 50, 93, 0.25) 0px 6px 12px -2px, rgba(0, 0, 0, 0.3) 0px 3px 7px -3px;
  }
  .textNewsThanks{
    /* font-weight: bold; */
    font-size: 17px;
    padding: 10px 10px 0px 10px;
    margin: 0px 0px;

  }
  .closeThank{
    font-size: 20px;
    cursor: pointer;
  }

  @media screen and (max-width:1460px){
    .favourite{
        width: 80vw;
    }
  }

  @media screen and (max-width:800px){
    .favourite{
        width: 7cm;
    }
  }
  @media screen and (max-width : 700px){
    .favourite{
        /* margin-left: 10vw; */
        width: 80vw;
    }
    .overlayNews{
        left: 20%;
    }
  }

  @media screen and (max-width:600px){
    .aboutHome{
        font-size: 40px;
    }
    .tagLineHome{
        font-size: 25px;
    }
