        font-size: 34px;
    }
    .blogHomeName{
        font-size: 22px;
    }
    .blogHomeDate{
        font-size: 14px;
    }
  }

  @media screen and (max-width:550px){
    .overlayNews{
        left: 15%;
    }
  }


  @media screen and (max-width:500px){
