        width: .3cm;
        height: .3cm;
    }
    .aboutHome{
        font-size: 30px;
    }
    .tagLineHome{
        font-size: 17px;
    }
    .latestBlogsHome{
        font-size: 30px;
    }
    .blogHomeName{
        font-size: 17px;
    }
