.Blogs{
    padding-left: 10vw;
  padding-right: 10vw;
  padding-top: 3vh;
  text-align: left;
}
.blogL{
    color: slateblue;
}

.blog{
  width: 13cm;
  /* height: 6cm; */
  /* border: 1px solid black; */
  padding: 0.5vh 1vw 0vh 1vw;
  border: 0.7px solid gray;
  border-radius: 7px;
  box-shadow: rgba(50, 50, 93, 0.25) 0px 6px 12px -2px, rgba(0, 0, 0, 0.3) 0px 3px 7px -3px;
  
}
.blog:hover{
  transform: scale(1.13 );
  transition: .12s;
  box-shadow:rgba(0,0,0,0.5) 0px 5px 15px, inset rgba(0,0,0,0.1) 0px -10px 20px;
}
.blogSection{
  display: flex;
  flex-wrap: wrap;
  column-gap: 3vw;
  row-gap: 3vh;
  padding-left: 5vw;
}
.blogNameCat:hover{
  text-decoration: underline;
  text-decoration-color: blueviolet;

}
